<template>
    <div>
        <el-select size="mini" :value="templateId" @change="setTemplateId" placeholder="请选择">
            <el-option
                    v-for="item in list"
                    :key="item.template_id"
                    :label="item.template_name"
                    :value="item.template_id">
            </el-option>
        </el-select>
    </div>
</template>

<script>
    import * as api from "@/api";

    export default {
        name: "SelectFreight",
        props:['templateId'],
        data() {
            return {
                loading: false,
                list: [],
            }
        },
        created() {
            this.getList();
        },
        methods: {
            setTemplateId(id){
                   this.$emit("input",id)
            },
            getList() {
                this.loading = true;
                api.post("/shop/admin.freight/getIndex", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.loading = false;
                    this.list = res.list;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },

    }
</script>

